.card {
    display: flex;
    flex-direction: column;
    background-color: white;
    border: 1px solid gainsboro;
    border-radius: 5px;
    padding: 25px;
    cursor: pointer;
    -moz-osx-font-smoothing: grayscale;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.15s ease-out;
}
.card h2 {
    margin-bottom: 0;
}
.card h3 {
    margin-top: 10px;
}
.card img {
    margin: 0 auto;
    border-radius: 50%;
    width: 80%;
}
.card:hover {
    transform: scale(1.05);
}
.card:hover:after {
    opacity: 1;
}

.card::after {
    opacity: 0;
    position: absolute;
    top: 0;
    right: 0;
    content: "";
    width: 100%;
    height: 100%;
    box-shadow:  6px 6px 12px #00000020,
    -6px -6px 12px #00000001;
    transition: opacity 0.15s ease-out;
}
